/* APP */
  .App {
    text-align: center;
  }
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  .App-link {
    color: #61dafb;
  }
  
/* HOME */
  .home {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: #61dafb;
  color:#282c34;
  }
  .home nav ul li {
    list-style: none;
    display: inline-block;
    margin-right: 20px;
    font-weight: 700;
    font-size: 1.5rem;
  }
  .home a {
    text-decoration: none;
    color: #282c34;
    padding: 10px;
  }
  .home a:hover {
    color:#fff;
  }
  .home li:first-child {
    text-decoration: underline;
  }

/* FOOTER */
  .footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    background-color: #61dafb;
    color:#282c34;
  }
  .footer nav ul li {
    display: inline-block;
    list-style: none;
    margin-left: 10px;
  }
  .footer a {
    text-decoration: none;
    color: #282c34;
    font-weight: 700;
    padding: 10px;
  }
  .footer a:hover{
    background-color: #fff;
    border-radius: 10px;
  }
  .footer p {
    font-weight: 700;
    margin-right: 10px;
  }

/* MEDIA */
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }

/* KEYFRAMES */
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
